#header .container,
#features .feature,
#new-reviews .review .review-head,
#freelancers .card-info,
#freelancers .card-last-review .review-head,
#freelancers .card-more,
#freelancer .card-actions,
#freelancer #reviews .review-head,
#contacts dl,
.form .buttons,
#other-freelancers .freelancer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

a.btn,
.btn {
  text-align: center;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}

[class*=icon-],
#freelancer #reviews .review .likes span {
  display: inline-block;
  vertical-align: middle;
}

.slick-dots {
  padding: 0;
  margin: .6rem 0 0 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  margin: 0 0.25rem;
  padding: 0;
  cursor: pointer;
  line-height: 1;
  vertical-align: top;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 0.375rem;
  height: 0.375rem;
  border: none;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border-radius: 50%;
  outline: none;
  background: #e8e8e8;
  margin: 0;
}

.slick-dots li:hover button,
.slick-dots li.slick-active button {
  background-color: #245ec3;
  border-color: #245ec3;
}

.checkbox input + span:after,
.checkbox input:checked + span:after {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #245ec3;
}

.checkbox {
  display: block;
  font-size: 0;
}

.checkbox input {
  display: none;
}

.checkbox span {
  display: inline-block;
  position: relative;
  padding-left: 1.5rem;
  font-size: 0.875rem;
  cursor: pointer;
}

.checkbox span a {
  text-decoration: none;
}

.checkbox span a:hover {
  text-decoration: underline;
}

.checkbox input + span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 0.188rem;
  border: 0.063rem solid #e7e8ea;
  background-color: #ffffff;
}

.checkbox input + span:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  display: none;
}

.checkbox:hover input + span:after,
.hover.checkbox input + span:after {
  display: block;
  opacity: 0.5;
}

.checkbox input:checked + span:after {
  display: block;
}

.checkbox:hover input:checked + span:after {
  opacity: 1;
}

.checkbox:last-child {
  margin-bottom: 0;
}

#header .container #menu > ul > li ul,
#quick-search .search-results,
.sort ul {
  opacity: 0;
  visibility: hidden;
  transition: 0.3s all;
}

#header .container #menu > ul > li.active ul,
#quick-search .search-results.active,
.sort.active ul {
  opacity: 1;
  visibility: visible;
}

#header .container #menu > ul > li ul,
#quick-search .search-results,
.sort ul {
  position: absolute;
  right: 0;
  top: calc(100% + .5rem);
  box-shadow: 0 0.125rem 0.625rem rgba(0, 0, 0, 0.04);
  border-radius: 0.25rem;
  background-color: #ffffff;
}

#header .container #menu > ul > li ul > li,
#quick-search .search-results > li,
.sort ul > li {
  font-size: 0.875rem;
  padding: 1rem;
  border-bottom: 1px solid #e8e8e8;
}

#header .container #menu > ul > li ul > li > a,
#quick-search .search-results > li > a,
.sort ul > li > a {
  display: block;
  padding: 1rem;
  margin: -1rem;
}

#header .container #menu > ul > li ul > li:hover > a,
#quick-search .search-results > li:hover > a,
.sort ul > li:hover > a,
#header .container #menu > ul > li ul > li.active > a,
#quick-search .search-results > li.active > a,
.sort ul > li.active > a {
  color: #245ec3;
}

#header .container #menu > ul > li ul > li:last-child,
#quick-search .search-results > li:last-child,
.sort ul > li:last-child {
  border-bottom: 0;
}

.slider-items .slick-arrow,
#new-reviews .slick-arrow {
  position: absolute;
  top: -3.5rem;
  z-index: 10;
  border: none;
  text-align: center;
  text-indent: -9999px;
  width: 3rem;
  height: 3rem;
  border-radius: 0.25rem;
  background-color: #ffffff;
}

.slider-items .slick-arrow:before,
#new-reviews .slick-arrow:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.75rem;
  height: 0.75rem;
}

.slider-items .slick-prev.slick-arrow,
#new-reviews .slick-prev.slick-arrow {
  right: 4.25rem;
}

.slider-items .slick-prev.slick-arrow:before,
#new-reviews .slick-prev.slick-arrow:before {
  background: url("../img/icon-arr-prev.svg") no-repeat;
  transform: translate(-50%, -50%) rotate(180deg);
}

.slider-items .slick-next.slick-arrow,
#new-reviews .slick-next.slick-arrow {
  right: 0.75rem;
}

.slider-items .slick-next.slick-arrow:before,
#new-reviews .slick-next.slick-arrow:before {
  background: url("../img/icon-arr-next.svg") no-repeat;
}

