:root {
  font-family: "Panton";
  color: #393e46;
  font-size: 1rem;
  background: #f9f9f9;
}

* {
  outline: none;
}

a {
  color: #393e46;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

i {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

ul.ul-reset,
.ul-reset ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

::-moz-placeholder {
  opacity: 1;
}

::-webkit-input-placeholder {
  opacity: 1;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  font-family: "Panton";
}

#page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  max-width: 1140px;
  width: 90%;
  margin: 0 auto;
}

.title {
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.title.-h1 {
  color: #15181e;
  font-size: 2.5rem;
}

.title.-h2 {
  font-size: 1.5rem;
}

.title.-h3 {
  font-size: 1.25rem;
}

.title.-h5 {
  font-size: 1.125rem;
}

.title.-h6 {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  margin-top: -1rem;
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  grid-auto-rows: auto;
}

.grid.col-3 {
  grid-template-columns: repeat(3, 1fr);
}

.grid.col-2 {
  grid-template-columns: repeat(2, 1fr);
}

.section {
  background: #fff;
  padding: 2.5rem 0;
}

.row-cols {
  margin-left: -0.75rem;
  margin-right: -0.75rem;
  display: flex;
  flex-wrap: wrap;
}

.row-cols [class*=col-] {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
}

.row-cols [class*=col-].col-25 {
  width: calc(25% - 1.5rem);
}

.row-cols [class*=col-].col-33 {
  width: calc(100% / 3 - 1.5rem);
}

.row-cols [class*=col-].col-50 {
  width: calc(50% - 1.5rem);
}

.row-cols.-margin-2 {
  margin-left: -0.5rem;
  margin-right: -0.5rem;
}

.row-cols.-margin-2 [class*=col-] {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.row-cols.-margin-2 [class*=col-].col-25 {
  width: calc(25% - 1rem);
}

.row-cols.-margin-2 [class*=col-].col-33 {
  width: calc(100% / 3 - 1rem);
}

.row-cols.-margin-2 [class*=col-].col-50 {
  width: calc(50% - 1rem);
}

.row-cols.-margin-3 {
  margin-left: -0.25rem;
  margin-right: -0.25rem;
}

.row-cols.-margin-3 [class*=col-] {
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

.row-cols.-margin-3 [class*=col-].col-16 {
  width: calc(100% / 6 - 0.5rem);
}

.row-cols.-margin-3 [class*=col-].col-25 {
  width: calc(25% - 0.5rem);
}

.row-cols.-margin-3 [class*=col-].col-33 {
  width: calc(100% / 3 - 0.5rem);
}

.row-cols.-margin-3 [class*=col-].col-66 {
  width: calc(100% / 3 * 2 - 0.5rem);
}

.row-cols.-margin-3 [class*=col-].col-50 {
  width: calc(50% - 0.5rem);
}

.slider-items {
  display: none !important;
}

.slider-items .slick-arrow.slick-next {
  right: 0;
}

#main {
  padding: 3rem 0;
}

.grid-main {
  display: grid;
  grid-template-areas: "content sidebar";
  grid-template-columns: 75% 1fr;
  grid-gap: 1.5rem;
  margin-bottom: 5rem;
}

.grid-main #content {
  grid-area: content;
}

.grid-main #sidebar {
  grid-area: sidebar;
}

.grid-main:last-child {
  margin-bottom: 0;
}

.breadcrumbs {
  font-size: 0;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  line-height: 1.5rem;
}

.breadcrumbs .icon-sep {
  background: url("../img/icon-arr-path.svg") no-repeat;
  width: 0.313rem;
  height: 0.5rem;
  margin: 0 0.75rem;
}

.breadcrumbs a,
.breadcrumbs span {
  color: #393e46;
  font-size: 0.875rem;
}

.breadcrumbs.-center {
  justify-content: center;
}

.breadcrumbs + .title {
  margin-bottom: 3rem;
}

.rating {
  display: flex;
  align-items: center;
  font-size: 0;
}

.rating > span {
  margin-left: 0.5rem;
  font-size: .875rem;
}

.link-category {
  color: #245ec3;
  display: inline-block;
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 1.5rem;
  border-radius: 0.25rem;
  border: 1px solid #e8e8e8;
  padding: 0 .5rem;
}

.link-category:hover,
.link-category.active {
  background-color: #245ec3;
  color: #fff;
  border-color: #245ec3;
}

.other-accounts {
  display: flex;
}

.other-accounts a {
  margin-left: 0.25rem;
}

.other-accounts a i {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.125rem;
}

.other-accounts a i.icon-fl {
  background: url("../img/icon-fl.svg") no-repeat;
}

.other-accounts a i.icon-weblancer {
  background: url("../img/icon-weblancer.svg") no-repeat;
}

.other-accounts a i.icon-habr {
  background: url("../img/icon-habr.svg") no-repeat;
}

.other-accounts a i.icon-upwork {
  background-image: url(../img/sprite.png);
  background-position: -34px 0px;
  width: 24px;
  height: 24px;
}

.other-accounts a i.icon-kwork {
  background-image: url(../img/sprite.png);
  background-position: 0px 0px;
  width: 24px;
  height: 24px;
}

.other-accounts a:first-child {
  margin-left: 0;
}

.scroll-pane {
  width: 100%;
  overflow: auto;
}

.socials {
  display: flex;
}

.socials a {
  width: 2.5rem;
  height: 2.5rem;
  border: 0.063rem solid #e8e8e8;
  background-color: #ffffff;
